import React, { useState } from 'react';
import { Upload, Button, Message, Progress, Divider, Typography, Space } from '@arco-design/web-react';
import { IconUpload, IconCloudDownload, IconCopy } from '@arco-design/web-react/icon';
import '@arco-design/web-react/dist/css/arco.css';
import './upload.css'; // CSS

const { Title, Text } = Typography;
const FileUploadPage = () => {
  const [fileList, setFileList] = useState([]);
const allowedFileTypes = [
  'image/jpeg',
  'image/png',
  'application/pdf',
  'text/plain',
  'application/msword',
  'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
  'application/vnd.android.package-archive', 
  'application/x-msdownload',
  'application/x-rar-compressed',
  'application/zip',
  'video/mp4',
  'audio/mpeg',
  'audio/wav',
  'audio/x-flac',
  'text/markdown',
  'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
  'application/vnd.openxmlformats-officedocument.presentationml.presentation', 
  'application/vnd.ms-powerpoint',           
];

const AllowType = [
  'JPG', 'PNG', 'PDF', 'TXT', 'DOC', 'DOCX', 'APK', 'EXE', 'RAR', 'ZIP', 'MP4', 'MP3', 'WAV', 'FLAC', 'MD', 'PPT', 'XLSX'
];

  const handleChange = ({ file, fileList = [] }) => {
    if (!file) {
      return;
    }
    setFileList(fileList);
    if (file.status === 'done') {
      Message.success(`${file.name} `);
    } else if (file.status === 'error') {
      Message.error(`${file.name} `);
    }
  };

  const navigateToDownloadStation = () => {
    // React RouteruseHistory hookReact Router
    // React Routerwindow.location.href
    // 
    // import { useHistory } from 'react-router-dom';
    // const history = useHistory();
    // history.push('/list');
    
    // window.location.href
    window.location.href = '/list';
    // history.push('/list');
  };

  const navigateToClipBoard = () => {
    // React RouteruseHistory hookReact Router
    // React Routerwindow.location.href
    // 
    // import { useHistory } from 'react-router-dom';
    // const history = useHistory();
    window.location.href = '/clipboard';
    // history.push('/clipboard');
  }

  const handleRemove = (file) => {
    if (!file) {
      return;
    }
    setFileList(fileList.filter(item => item.uid !== file.uid));
  };

  const beforeUpload = (file) => {
    if (!file) {
      return false;
    }
    const isAllowedFileType = allowedFileTypes.includes(file.type);
    if (!isAllowedFileType) {
      const allowedTypesString = AllowType.join(', ');
      Message.error(`${allowedTypesString}`);
      return false;
    }
    const isLt20M = file.size / 1024 / 1024 < 50;
    if (!isLt20M) {
      Message.error(' 50MB!');
      return false;
    }
    return true;
  };

  return (
    <div className="file-upload-container">
      <div className="header-container">
        <Title heading={2} className="title"></Title>
        <Text type="secondary" className="description">
          : JPG, PNG, PDF, TXT, DOC, DOCX, APK, EXE, RAR, ZIP, MP4, MP3, WAV, FLAC, MD, PPT, XLSX, 50MB
        </Text>
      </div>
      <Divider />
      <Space direction="vertical" size="large" style={{ width: '100%' }}>
        <div className="upload-button-container">
          <Upload
            action="/upload"  // URL
            onChange={handleChange}
            onRemove={handleRemove}
            beforeUpload={beforeUpload}
            progress={{
              strokeWidth: 2,
              showText: true,
              format: percent => `${percent}%`,
            }}
          >
            <Button type="primary" icon={<IconUpload />} className="upload-button"></Button>
          </Upload>
        </div>
        {fileList.length > 0 && fileList.map(file => (
          file.status === 'uploading' && (
            <Progress 
              key={file.uid}
              percent={file.percent} 
              status="active" 
              size="small"
              className="progress"
            />
          )
        ))}
      </Space>
      <Button 
        type="primary" 
        onClick={navigateToDownloadStation}
        icon={<IconCloudDownload />}
        style={{ marginTop: '16px' }} // 
      >
        
      </Button>
      <Button 
        type="primary" 
        onClick={navigateToClipBoard}
        icon={<IconCopy />}
        style={{ marginTop: '16px' }} // 
      >
        
      </Button>
    </div>
  );
};

export default FileUploadPage;
