import React, { useEffect, useState } from 'react';
import { Typography, List, Button, Message, Space, Modal, Input } from '@arco-design/web-react';
import { IconDownload, IconDelete } from '@arco-design/web-react/icon';
import axios from 'axios';
import './file.css'; // CSS

const { Title, Text } = Typography;
// const local = 'http://localhost:8080';
const local = ''

const Filelist = () => {
  const [fileList, setFileList] = useState([]);
  const [password, setPassword] = useState('');
  const [visible, setVisible] = useState(false);
  const [currentFile, setCurrentFile] = useState('');

  useEffect(() => {
    // 
    axios.get(`${local}/file`)
      .then(response => {
        setFileList(response.data.files);
      })
      .catch(error => {
        Message.error('');
        console.error(error);
      });
  }, []);

  const handleDownload = (filename) => {
    // 
    window.location.href = `${local}/download?filename=${filename}`;
  };

  const handleDelete = () => {
    // 
    axios.post(`${local}/list/delete`, {
      filename: currentFile,
      password: password,
    })
      .then(response => {
        Message.success(response.data.message);
        setFileList(fileList.filter(file => file !== currentFile));
        setVisible(false);
        setPassword('');
      })
      .catch(error => {
        Message.error('');
        console.error(error.response.data.error);
      });
  };

  const showDeleteConfirm = (filename) => {
    setCurrentFile(filename);
    setVisible(true);
  };

  return (
    <div className="file-list-container">
      <div className="header-container">
        <Title heading={2} className="title"></Title>
        <Text type="secondary" className="description">
          
        </Text>
      </div>
      <Space direction="vertical" size="large" style={{ width: '100%' }}>
        <List
          dataSource={fileList}
          render={(item) => (
            <List.Item
              key={item}
              extra={
                <>
                  <Button onClick={() => showDeleteConfirm(item)}>{<IconDelete />}</Button>
                  <Button type="primary" icon={<IconDownload />} onClick={() => handleDownload(item)}></Button>
                </>
              }
            >
              <Typography.Text>{item}</Typography.Text>
            </List.Item>
          )}
        />
      </Space>
      <Modal
        title=""
        visible={visible}
        onOk={handleDelete}
        onCancel={() => setVisible(false)}
        okText=""
        cancelText=""
      >
        <Input.Password
          placeholder=""
          value={password}
          onChange={(value) => setPassword(value)}
        />
      </Modal>
    </div>
  );
};

export default Filelist;
